    //     //this.form.gender = "";
    //   }
    // },
    // SurnameBlur() {
    //   var surnameinput = new RegExp("^[a-zA-Z]+$");
    //   if (this.form.surname != "" && !surnameinput.test(this.form.surname)) {
    //     const toast = this.$createToast({
    //       time: 3500,
    //       type: "txt",
    //       txt: "只能输入全英文且不能为空"
    //     });
    //     toast.show();
    //     //this.form.surname = "";
    //   }
    // },
    // givenameBlur() {
    //   var surnameinput = new RegExp("^[a-zA-Z]+$");
    //   if (this.form.givename != "" && !surnameinput.test(this.form.givename)) {
    //     const toast = this.$createToast({
    //       time: 3500,
    //       type: "txt",
    //       txt: "只能输入全英文且不能为空"
    //     });
    //     toast.show();
    //     //this.form.givename = "";
    //   }
    // },
    // birthDateBlur() {
    //   var birthinput = new RegExp(
    //     "^(19[0-9][0-9]|200[0-9])-(0?[1-9]|1[0-2])-(0?[1-9]|[1-2][0-9]|3[0-1])$"
    //   );
    //   if (this.form.datebirth != "" && !birthinput.test(this.form.datebirth)) {
    //     const toast = this.$createToast({
    //       time: 3500,
    //       type: "txt",
    //       txt: "生日输入有误;请输入正确的格式( YYYY-MM-DD )"
    //     });
    //     toast.show();
    //     //this.form.datebirth = "";
    //   }
    // },
    // HkidBlur() {
    //   console.log(this.form.HKID1);
    //   var Hkidinput1 = new RegExp("^.{8}$");
    //   if (this.form.HKID1.trim() != "" && !Hkidinput1.test(this.form.HKID1)) {
    //     const toast = this.$createToast({
    //       time: 3500,
    //       type: "txt",
    //       txt: "输入长度只能为8位数"
    //     });
    //     toast.show();
    //     //this.form.HKID1 = "";
    //   }
      // else {
      //   this.$http
      //     .post(
      //       "https://dev-wesurance814.com/wesuapi5/validate/validateHKIDWithLng",
      //       {
      //         language: "tc",
      //         hkid: 'P1032651'
      //       }
      //     )
      //     .then(res => {
      //       console.log(res);
      //     });
      // }
    // },
    // passportBlur() {
    //   var passportInput = new RegExp("^[0-9]*$");
    //   if (
    //     this.form.HKID_passport.trim() != "" &&
    //     !passportInput.test(this.form.HKID_passport)
    //   ) {
    //     const toast = this.$createToast({
    //       time: 3500,
