    //     });
    //     toast.show();
    //     //this.form.HKID_passport = "";
    //   }
    // },
    //点击编辑回到编辑页后,循环遍历对象生产一个全新的对象
    getNemObj() {
      if (this.$store.state.userInfo) {
        console.log("当userInfo有值时,把值赋值给form");
        this.form = {};
        for (var key in this.formdata) {
          this.form[key] = this.formdata[key];
        }
        //判断再次进入信息编辑页的时候passport是否有值
        if(this.form.HKID_passport != ''){
          this.select = 2
        }
      }
    }
    
    
  },
  computed: {}
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.form-wrap {
  padding: 20px;
  box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.14);
  // line-height: 65px;
  margin-bottom: 30px;
  p {
    display: flex;
    justify-content: space-between;
    .member {
      color: @blue;
      font-weight: bold;
      font-size: 32px;
    }
    .member-num {
      color: @blue;
      font-weight: bold;
      font-size: 32px;
      margin-left: 15px;
    }
    .member-title {
      font-weight: bold;
      font-size: 32px;
      color: rgb(0, 0, 0);
      font-family: HelveticaNeue-Medium;
    }
  }
  .Gender {
    height: 75px;
    width: 100%;
    border-radius: 15px;
    font-size: 30px;
    border: 2px solid rgba(173, 175, 175, 0.199);
    text-indent: 20px;
    font-weight: bold;
  }
  input::-webkit-input-placeholder {
    color: rgb(233, 233, 233);
    letter-spacing: 2px;
    font-weight: 100;
  }
  .form-item {
    p {
      color: rgb(0, 0, 0);
    }
    line-height: 35px;
    margin-top: 20px;
  }
  .HKID {
    width: 316px;
    height: 56px;
    color: white;
    font-size: 30px;
    outline: none;
    border: none;
    background: rgb(236, 235, 235);
    border-radius: 10px;
    margin-top: 40px;
  }
  .passport {
    width: 316px;
    height: 56px;
    padding: 0;
    text-align: center;
    color: white;
    font-size: 30px;
    background: rgb(236, 235, 235);
    outline: none;
    border: none;
    border-radius: 10px;
    margin-top: 40px;
  }
  .blue {
    background: @blue;
  }
  .HK {
    margin-top: 30px;
    font-size: 32px;
    margin-bottom: 10px;
    .left {
      display: inline-block;
    }
    .right {
      display: inline-block;
    }
    .hkNumber {
      color: rgb(0, 0, 0);
      // font-weight: bold;
      font-family: HelveticaNeue;
    }
    span {
      .IDnumber {
        width: 235px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        padding-right: 15px;
        border: 2px solid rgba(173, 175, 175, 0.199);
        margin-left: 20px;
        text-align: right;
      }
      .num {
        width: 75px;
        height: 60px;
        border-radius: 13px;
        font-size: 30px;
        border: 2px solid rgba(173, 175, 175, 0.199);
        text-align: center;
      }
      .width {
        width: 300px;
      }
    }
  }
}
</style>


