 * @Description: In User Settings Edit
 * @Author: your name
 * @Date: 2019-08-29 10:30:48
 * @LastEditTime: 2019-09-12 16:36:41
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <div class="plan-item">
      <p class="plan">Your plan</p>
      <p class="travelead">TraveLead(Essential)</p>
    </div>

    <div class="pre-item">
      <p class="premium">Premium</p>
      <p class="price">HK$190.00</p>
    </div>

    <div class="date">
      <span>
        <p class="date-title">Policy is effective from</p>
        <p class="date-date">{{departureData}}</p>
      </span>
      <span>
        <p class="date-title">End date</p>
        <p class="date-date">{{returnData}}</p>
      </span>
      <span @click="back" class="edit">Edit</span>
    </div>
    <!-- 保单人信息 -->
    <WOrder v-if="isShow" :indexList="0" :userData="this.$store.state.protectorInfo"></WOrder>
    <!-- 调用组件 -->
    <WOrder v-for="(item,index) in userInfo" :key="index" :indexList="index | newIndex(isShow)" :userData="item"></WOrder>

    <div class="button-bar">
      <router-link to="/Payment">
        <button class="proceed">Confirm</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import WOrder from "components/WOrder.vue";
export default {
  components: {
    WOrder
  },
  data() {
    return {
      isShow:false,
      //定义一个变量来显示DepartureData
      departureData:'',
      //定义一个变量来显示returnData
      returnData:''
    };
  },
  created() {
    const toast = this.$createToast({
      time: 1000,
      txt: "loading..."
    });
    toast.show();
    //如果vuex中有保单人数据
    if(this.$store.state.protectorInfo){
      this.isShow = true;
    }else{
      this.isShow = false;
    }
    //页面加载获取vuex中的时间
    this.getData();
    console.log(this.returnData);
    console.log(this.$store.state.travelInfo.date.returnDate);
    
  },
  computed: {
    userInfo() {
      return this.$store.state.userInfo;
    },
    // getDepartureData() {
    //   return this.$store.state.travelInfo.date.departureDate;
    // },
    // getReturnData() {
    //   return this.$store.state.travelInfo.date.returnDate;
    // },
  },
  //过滤器:判断保单人是否存在,存在的话序号加2
  filters:{
    newIndex(val,val2){
      if(val2 == true){
        return val+1
      }else{
        return val
      }
    }
  },
  methods: {
    back() {
      this.$router.push("/TravelInfo/back");
    },
    //创建一个方法来从vuex中获取时间显示时间
    getData(){
      this.departureData = this.$store.state.travelInfo.date.departureDate;
      this.returnData = this.$store.state.travelInfo.date.returnDate;
    }
  }
};
</script>

<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 34px;
  background: @background;
  .imgs {
    width: 100%;
  }
  .img {
    width: 100%;
    height: 100%;
  }
  .plan-item {
    line-height: 40px;
    .plan {
      color: rgb(221, 219, 219);
    }
    .travelead {
      font-family: PingFangHK-Regular;
      font-size: 36px;
    }
  }
  .pre-item {
    line-height: 40px;
    margin-top: 40px;
    .premium {
      color: rgb(221, 219, 219);
      font-family: PingFangHK-Regular;
    }
    .price {
      color: @blue;
      font-weight: bold;
      font-size: 42px;
    }
  }
  .date {
    display: flex;
    justify-content: space-between;
    margin-top: 100px;
    line-height: 45px;
    span {
      .date-title {
        color: rgb(221, 219, 219);
        font-family: PingFangHK-Regular;
      }
      .date-date {
        font-size: 30px;
      }
    }
    .edit {
      color: @blue;
    }
  }

  .button-bar {
    margin: 0 auto;
    text-align: center;
    margin-top: 54px;
    margin-bottom: 102px;
  }
  .proceed {
    width: 388px;
    height: 130px;
    line-height: 130px;
    border: none;
    border-radius: 10px;
    background: rgb(2, 164, 71);
    color: #fff;
    text-align: center;
    font-size: 36px;
  }
}
</style>


