 * @LastEditTime: 2019-09-09 14:40:32
 * @LastEditors: Please set LastEditors
 -->
<template>
  <div class="page-wrap">
    <!-- <div class="">Coverage Details</div> -->
    <div v-for="(item,index) in datalist" :key="index">
      <p class="title">{{index +1 }}.{{item.name}}</p>
      <p class="details-item">{{item.details}}</p>
      <p class="details" v-for="(items,i) in item.tnclist" :key="i">
        <span class="desc">{{items.desc}}</span>
        <span>{{items.insured_sum}}</span>
      </p>
    </div>
     <div class="button-bar">
        <button class="proceed" @click="proceed">Proceed</button>
       
      </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'

export default {
  data() {
    return {
      datalist: "",
    };
  },
   computed: {
    ...mapState([ 'product_id', 'language', 'plan'])
  },
  mounted() {
    // var productId = this.product_id;
    // console.log(productId);

    const params = {
      product_id: this.product_id,
      language: this.language
    };

    this.$http.post("/wesupromo/web/tpc/getProductTerms", params).then(res => {
      // console.log(res)
      this.datalist = res.data.product_terms_list;
      console.log(this.datalist[0].tnclist);

    // 
    });
  },
  methods:{
    proceed(){
      this.$router.go(-1);
    }
  }
};
</script>
<style lang="less" scoped>
@import "~style/mixin.less";
.page-wrap {
  padding: 30px;
  div {
    margin-top: 30px;
    .title {
      color: @blue;
      font-size: 35px;
      border-bottom: 1px solid rgb(209, 208, 208);
      padding-bottom: 20px;
    }
    .details-item{
         border-bottom: 1px solid rgb(209, 208, 208);
      padding-bottom: 20px;
      padding-top: 25px;
    }
    .details{
        display: flex;
        justify-content: space-between;
         border-bottom: 1px solid rgb(209, 208, 208);
      padding-bottom: 15px;
      padding-top: 25px;
        span{
             
      line-height: 35px;
        }
        .desc{
            display: inline-block;
            width: 70%;
        }
    }
  }
  .button-bar {
      text-align: center;
      margin-top: 54px;
      margin-bottom: 102px;
    }

    .proceed {
      width: 388px;
      height: 130px;
      line-height: 130px;
      border: none;
      border-radius: 10px;
      background: rgb(2, 164, 71);
      color: #fff;
      text-align: center;
      font-size: 36px;
    }
}
</style>


